@import url(https://fonts.googleapis.com/css2?family=Revalia&display=swap);
body {
  margin: 0;
  padding:0;
  background-color: rgb(23, 23, 23);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.footer-container {
    position: fixed;
}
.slideContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;

    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(86, 8, 68) 75%);
}
.slideGrid {
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    justify-items: center;
}
.slide {
    grid-row: 2/3;
}
.slideNav {
    position: relative;
    text-align: center;
}

.text {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    font-size: 250%;
    color: white;
}

.letter, .letterContainer {
    display: inline-block;
    padding: 0.1em;
}

.space {
    padding-right: 0.5em;
}

::-webkit-scrollbar {
  width: 20px;
}

/* ::-webkit-scrollbar-track {
  background-color: transparent;
} */

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
}

::-webkit-scrollbar-thumb {
  background-color: #d6dee1;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb {
    background-color: #d6dee1;
    border-radius: 20px;
    border: 6px solid transparent;
    background-clip: content-box;
  }
  
::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}
  
@media all and (max-width: 768px){
  ::-webkit-scrollbar {
      width: 0;
  }

  #responsive-div {  
    width: 100%;
  } 

  #responsive-image {  
    width: -webkit-max-content;  
    width: -moz-max-content;  
    width: max-content;
  } 
}
*,
*::before,
*::after {
  margin: 0;
}

.projects {
    /* font-family: "Quicksand", sans-serif; */
    display: grid;
    /* grid-template-rows: auto auto; */
    /* place-items: center; */
    height: 100vh;
    background: linear-gradient(0deg, rgb(0, 0, 0) 20%, rgb(86, 8, 68) 75%);
    overflow-y: scroll;
}

a {
    color: white;
    margin-top: 1.5em;
    margin-left: 1.3em;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    text-decoration: none;
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

a:hover { COLOR: #a8bbbf; TEXT-DECORATION: none; font-weight: none }

.reg_link, .router_link {
    margin: 0;
}

.project-container {
    display: flex;
    flex-wrap: wrap;
    justify-self: center;
    justify-content: center;
    max-width: 1200px;
    margin-block: 2rem;
    grid-gap: 2rem;
    gap: 2rem;
    padding-bottom: 5em;
    /* padding-top: 3em; */
}

.card {
    display: flex;
    flex-direction: column;
    width: clamp(22em, calc(20em + 2vw), 22em);
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    /* width: 25%; */
    overflow: hidden;
    border-radius: 1em;
    background: rgba(75, 75, 75, 0.25);
    outline: 1px solid rgba(255, 255, 255, 0.15);
    color: white;
}

.in-card {
    /* width: 80%; */
    color: white;
}

img, video {
    max-width: 100%;
    border-radius: 1em;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.card__body {
    padding: 1rem;
    flex-direction: column;
    grid-gap: .5rem;
    gap: .5rem;
}

h2 {
    margin-top: 0.5em;
}


p, ul {
    font-size: 1.1em;
}

ul {
    list-style-type: circle;
}

li {
    margin: 10px 0;
}

.tag {
    align-self: flex-start;
    padding: .25em .75em;
    border-radius: 1em;
    font-size: .75rem;
}

.card__footer {
    padding-left: 1em;
    padding-right: 1em;
    padding-bottom: 1em;
    margin-top: auto;
    color: rgb(255, 255, 255);
    font-size: 1.2em;
}

.project_footer{
    color: white;
}



/* click zoom images */

.zoom_img {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: equally-spaced;
}
  
.zoom_img label {
    position: relative;
}

.zoom_img img {
    width: 45%;
    object-fit: contain;
}
  
.zoom_img input:checked+img {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    background: rgba(75, 75, 75, 0.75);
    object-fit: contain;
}
  
.zoom_img input {
    width: 19vh;
    height: 19vh;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: -1;
}
.about-card {
    color: aliceblue;
    width: 60%;
}
